import { AfterViewInit, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { clear } from 'console';
import { mergeMap, filter } from 'rxjs/operators';
import { GoogleAnalyticsService } from 'src/app/services/googletag.service';
import { StoreService } from 'src/app/services/store.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-lista',
  templateUrl: './lista.component.html',
  styleUrls: ['./lista.component.scss']
})
export class ListaComponent implements OnInit, AfterViewInit {
  public layout = environment.layout;
  public totalItens = 0;
  public itensPorPag = 32;
  public ordenacao = 'Selecione';
  public nPaginas = 1;
  public activePag = 0;
  public slicedItems = [];
  public filtro = [];
  public peso = [];
  public loading = true;
  public estoque = [];
  public indisponivel = [];
  public filtrar = false;
  private exactMatch = false;
  formGeral: FormGroup;



  constructor(private activRoute: ActivatedRoute,
              private storeS: StoreService,
              private gTagS: GoogleAnalyticsService,
              private fb: FormBuilder,
              public store: StoreService,
    ) { }

  ngOnInit() {
    this.setupForm();
    this.activRoute.data.subscribe((res: any) => {
      if (res.exactMatch) {
        this.exactMatch = true;
      }
    });
    this.categorias();
    this.formGeral.reset();

    setTimeout(() => {
      this.updateSlicedItems();
      this.updateNumeroPaginas();
    });

  }

  ngAfterViewInit() {
    this.slicedItems = [...this.estoque, ...this.indisponivel];
  }


  selectedItensPorPag() {
    this.updateSlicedItems();
    this.updateNumeroPaginas();
  }
  setupForm(){
    this.formGeral = this.fb.group({
      status: [null],
      marca: [null],
      promo: [null]
    });
  }

  showFilters(){
    this.filtrar = true;
  }
  limparFiltros(){
    this.categorias();
    this.formGeral.reset();
  }


  categorias(){
    this.activRoute.params.subscribe(res => {
      this.loading = true;
      this.slicedItems = [];
      this.filtro = [];
      this.peso = [];
      this.totalItens = 0;
      switch (res.tipo) {
        case 'categoria':
          const paramId = this.activRoute.snapshot.queryParams.id;
          if (!paramId) {
            return;
          }
          this.storeS.getItensByCateg(paramId).subscribe((resB: any) => {
            for (let i = 0; i <= 200; i++) {
               if (resB[i]?.tabela?.vPromo != null){
               if (new Date() > new Date(resB[i]?.tabela?.vPromoData?.split('-'))){
                  resB[i].tabela.vPromo = null;
                }
              }
            }
            scrollTo(0, 300);
            this.slicedItems = [];
            this.filtro = [];
            this.peso = [];
            for (let i = 0; i <= 200; i++) {
              if (resB[i]) {
                this.slicedItems.push(resB[i]);
                this.filtro.push(resB[i]);
                this.filtro = this.filtro.filter((record, index) => this.filtro.findIndex(check => check.marcaJ?.marca === record.marcaJ?.marca && check.estoque != null) === index );
                this.peso.push(resB[i]);
                this.peso = this.peso.filter((record, index) => this.peso.findIndex(check => check.peso === record.peso && check.estoque != null && check.peso != null) === index);

                this.slicedItems = this.slicedItems.filter(item => item.status === 'A');
              }
              }
            this.totalItens = Object.keys(resB).length;
            [this.estoque, this.indisponivel] = this.partition(this.slicedItems, e => e.estoque?.estAtual > 0);
            this.slicedItems = [...this.estoque, ...this.indisponivel];
            // this.gTagS.viewItemList(this.slicedItems, res.termo.replace(/-/g, ' '));
            this.loading = false;

          });
          break;
        case 'search':
          this.storeS.getItensBySearchTerm(res.termo.replace(/-/g, ' '), this.exactMatch).subscribe((resC: any) => {
            for (let i = 0; i <= 500; i++) {
               if (resC[i]?.tabela?.vPromo != null){
               if (new Date() > new Date(resC[i]?.tabela?.vPromoData?.split('-'))){
                resC[i].tabela.vPromo = null;
                }
              }
            }
            scrollTo(0, 300);
            if (resC.length > 0) {
              this.slicedItems = resC;
              this.totalItens = resC.length;
              [this.estoque, this.indisponivel] = this.partition(this.slicedItems, e => e.estoque?.estAtual > 0);
              this.slicedItems = [...this.estoque, ...this.indisponivel];
              this.gTagS.viewItemList(this.slicedItems, res.termo.replace(/-/g, ' '));
            }
            this.loading = false;
          });
          break;
        default:
          this.loading = false;
          break;
      }
    });
  }
  changePage(index) {
    this.activePag = index;
    this.updateSlicedItems();
  }

  updateSlicedItems() {
    // this.slicedItems = this.slicedItems.slice(this.activePag * this.itensPorPag, this.activePag * this.itensPorPag + this.itensPorPag);
    this.slicedItems = [...this.estoque, ...this.indisponivel];

  }

  updateNumeroPaginas() {
    this.nPaginas = Math.ceil(this.slicedItems.length / this.itensPorPag);
    if (this.activePag >= this.nPaginas) {
      this.activePag = 0;
      this.updateSlicedItems();
    }
  }

  partition(array, filtro) {
    const pass = [];
    const fail = [];
    array.forEach((e, idx, arr) => (filtro(e, idx, arr) ? pass : fail).push(e));
    return [pass, fail];
  }


  ordenarLista(type: string) {
    switch (type) {
      case 'menorPreco':
        this.estoque.sort((a, b) => {
          return a.tabela?.vVenda < b.tabela?.vVenda ? -1 : a.tabela?.vVenda > b.tabela?.vVenda ? 1 : 0;
        });
        break;
      case 'maiorPreco':
        this.estoque.sort((a, b) => {
          return a.tabela?.vVenda > b.tabela?.vVenda ? -1 : a.tabela?.vVenda < b.tabela?.vVenda ? 1 : 0;
        });
        break;
      case 'alfabetica':
        this.estoque.sort((a, b) => {
          return a.item_linkWeb.linkWeb.titulo > b.item_linkWeb.linkWeb.titulo ? 1 : a.item_linkWeb.linkWeb.titulo < b.item_linkWeb.linkWeb.titulo ? -1 : 0;
        });
        break;
      default:
        break;
    }
    this.slicedItems = [...this.estoque, ...this.indisponivel];
  }
  filterpromo(){
    this.slicedItems = this.slicedItems.filter(item => item.tabela?.vPromo != null );
    this.totalItens = Object.keys(this.slicedItems).length;
    [this.estoque, this.indisponivel] = this.partition(this.slicedItems, e => e.estoque?.estAtual > 0);
    this.slicedItems = [...this.estoque, ...this.indisponivel];
    this.loading = false;


  }
  filterMarcas(event){
    this.slicedItems = this.slicedItems.filter(item => item.marcaJ?.marca === event.target.value  );
    this.filtro = this.slicedItems.filter((record, index) => this.filtro.findIndex(check => check.marcaJ?.marca === record.marcaJ?.marca) === index);
    this.peso = this.peso.filter(item => item.marcaJ?.marca === event.target.defaultValue  );
/*     this.peso = this.peso.filter((record, index) => this.peso.findIndex(check => check.peso === record.peso) === index);
 */

    this.totalItens = Object.keys(this.slicedItems).length;
    [this.estoque, this.indisponivel] = this.partition(this.slicedItems, e => e.estoque?.estAtual > 0);
    this.slicedItems = [...this.estoque, ...this.indisponivel];
    this.loading = false;

  }

  filterPeso(event){
    this.slicedItems = this.slicedItems.filter(item => item.peso === Number(event.target.defaultValue) );

    this.peso = this.slicedItems.filter((record, index) => this.peso.findIndex(check => check.peso === record.peso) === index);

    this.totalItens = Object.keys(this.slicedItems).length;
    [this.estoque, this.indisponivel] = this.partition(this.slicedItems, e => e.estoque?.estAtual > 0);
    this.slicedItems = [...this.estoque, ...this.indisponivel];
    this.loading = false;

  }
  faixaPreco(event){
    switch (event.target.value) {
      case '100':
        this.categoriaFilter(event.target.value);
        break;
      case '200':
        this.categoriaFilter(event.target.value);
        break;
      case '500':
        this.categoriaFilter(event.target.value);
        break;
        case '500+':
          this.categoriaFilter(event.target.value);
          break;
      default:
        break;
    }
    this.slicedItems = [...this.estoque, ...this.indisponivel];

  }

  categoriaFilter(event){
    this.activRoute.params.subscribe(res => {
      this.loading = true;
      this.slicedItems = [];
      this.filtro = [];
      this.peso = [];
      this.totalItens = 0;
      switch (res.tipo) {
        case 'categoria':
          const paramId = this.activRoute.snapshot.queryParams.id;
          if (!paramId) {
            return;
          }
          this.storeS.getItensByCateg(paramId).subscribe((resB: any) => {
            this.slicedItems = [];
            for (let i = 0; i <= 200; i++) {
              if (this.slicedItems.filter(item => item.tabela?.vPromo != null )){
                this.formGeral.patchValue({
                  promo: '',
                  peso: '',

                });              }
              if (resB[i]) {
                if (event === '100'){
                  this.slicedItems.push(resB[i]);
                  this.slicedItems = this.slicedItems.filter(item => item.tabela?.vVenda < 100  );
                  this.filtro.push(resB[i]);
                  this.filtro = this.filtro.filter((record, index) => this.filtro.findIndex(check => check.marcaJ?.marca === record.marcaJ?.marca && check.estoque != null && check.tabela?.vVenda < 100) === index );
                  this.peso.push(resB[i]);
                  this.peso = this.peso.filter((record, index) => this.peso.findIndex(check => check.peso === record.peso && check.estoque != null && check.peso != null && check.tabela?.vVenda < 100) === index);

                }
                if (event === '200'){
                  this.slicedItems.push(resB[i]);
                  this.slicedItems = this.slicedItems.filter(item => item.tabela?.vVenda > 100 && item.tabela?.vVenda < 200 );
                  this.filtro.push(resB[i]);
                  this.filtro = this.filtro.filter((record, index) => this.filtro.findIndex(check => check.marcaJ?.marca === record.marcaJ?.marca && check.estoque != null && check.tabela?.vVenda > 100 && check.tabela?.vVenda < 200) === index );
                  this.peso.push(resB[i]);
                  this.peso = this.peso.filter((record, index) => this.peso.findIndex(check => check.peso === record.peso && check.estoque != null && check.peso != null && check.tabela?.vVenda > 100 && check.tabela?.vVenda < 200) === index);



                }
                if (event === '500'){
                  this.slicedItems.push(resB[i]);
                  this.slicedItems = this.slicedItems.filter(item => item.tabela?.vVenda > 200 && item.tabela?.vVenda < 500 );
                  this.filtro.push(resB[i]);
                  this.filtro = this.filtro.filter((record, index, estoque) => this.filtro.findIndex(check => check.marcaJ?.marca === record.marcaJ?.marca && check.estoque != null && check.tabela?.vVenda < 200 && check.tabela?.vVenda < 500 ) === index );
                  this.peso.push(resB[i]);
                  this.peso = this.peso.filter((record, index) => this.peso.findIndex(check => check.peso === record.peso && check.estoque != null && check.peso != null && check.tabela?.vVenda < 200  && check.tabela?.vVenda < 500) === index);

                }
                if (event === '500+'){
                  this.slicedItems.push(resB[i]);
                  this.slicedItems = this.slicedItems.filter(item => item.tabela?.vVenda > 500  );
                  this.filtro.push(resB[i]);
                  this.filtro = this.filtro.filter((record, index, estoque) => this.filtro.findIndex(check => check.marcaJ?.marca === record.marcaJ?.marca && check.estoque != null && check.tabela?.vVenda > 500 ) === index );
                  this.peso.push(resB[i]);
                  this.peso = this.peso.filter((record, index) => this.peso.findIndex(check => check.peso === record.peso && check.estoque != null && check.peso != null && check.tabela?.vVenda > 500 ) === index);

                }

              }
              }
            this.totalItens = Object.keys(resB).length;
            [this.estoque, this.indisponivel] = this.partition(this.slicedItems, e => e.estoque?.estAtual > 0);
            this.slicedItems = [...this.estoque, ...this.indisponivel];
            // this.gTagS.viewItemList(this.slicedItems, res.termo.replace(/-/g, ' '));
            this.loading = false;

          });
          break;
          case 'search':
          this.storeS.getItensBySearchTerm(res.termo.replace(/-/g, ' '), this.exactMatch).subscribe((resC: any) => {
        if (resC.length > 0) {
        this.slicedItems = resC;
        this.totalItens = resC.length;
        [this.estoque, this.indisponivel] = this.partition(this.slicedItems, e => e.estoque?.estAtual > 0);
        this.slicedItems = [...this.estoque, ...this.indisponivel];
        this.gTagS.viewItemList(this.slicedItems, res.termo.replace(/-/g, ' '));
        if (event === '100'){
          this.slicedItems = this.slicedItems.filter(item => item.tabela?.vVenda < 100  );
          this.filtro = this.filtro.filter((record, index) => this.filtro.findIndex(check => check.marcaJ?.marca === record.marcaJ?.marca && check.estoque != null && check.tabela?.vVenda < 100) === index );
          this.peso = this.peso.filter((record, index) => this.peso.findIndex(check => check.peso === record.peso && check.estoque != null && check.peso != null && check.tabela?.vVenda < 100) === index);

        }
        if (event === '200'){
          this.slicedItems = this.slicedItems.filter(item => item.tabela?.vVenda > 100 && item.tabela?.vVenda < 200 );
          this.filtro = this.filtro.filter((record, index) => this.filtro.findIndex(check => check.marcaJ?.marca === record.marcaJ?.marca && check.estoque != null && check.tabela?.vVenda > 100 && check.tabela?.vVenda < 200) === index );
          this.peso = this.peso.filter((record, index) => this.peso.findIndex(check => check.peso === record.peso && check.estoque != null && check.peso != null && check.tabela?.vVenda > 100 && check.tabela?.vVenda < 200) === index);



        }
        if (event === '500'){
          this.slicedItems = this.slicedItems.filter(item => item.tabela?.vVenda > 200 && item.tabela?.vVenda < 500 );
          this.filtro = this.filtro.filter((record, index, estoque) => this.filtro.findIndex(check => check.marcaJ?.marca === record.marcaJ?.marca && check.estoque != null && check.tabela?.vVenda < 200 && check.tabela?.vVenda < 500 ) === index );
          this.peso = this.peso.filter((record, index) => this.peso.findIndex(check => check.peso === record.peso && check.estoque != null && check.peso != null && check.tabela?.vVenda < 200  && check.tabela?.vVenda < 500) === index);

        }
        if (event === '500+'){
          this.slicedItems = this.slicedItems.filter(item => item.tabela?.vVenda > 500  );
          this.filtro = this.filtro.filter((record, index, estoque) => this.filtro.findIndex(check => check.marcaJ?.marca === record.marcaJ?.marca && check.estoque != null && check.tabela?.vVenda > 500 ) === index );
          this.peso = this.peso.filter((record, index) => this.peso.findIndex(check => check.peso === record.peso && check.estoque != null && check.peso != null && check.tabela?.vVenda > 500 ) === index);

        }
      }
        this.loading = false;
    });
          break;
  default:
    this.loading = false;
    break;

      }
    });

  }
}
