import { HttpClient } from "@angular/common/http";
import { Injectable, PLATFORM_ID, Inject } from "@angular/core";
import { environment } from "./../../environments/environment";
import { Collx } from "./../core/Collx";
import { ICart } from "./cart.interface";
import { IItem } from "./item.interface";
import { isPlatformBrowser, isPlatformServer } from "@angular/common";

import { Observable } from "rxjs";
const cartInit: ICart = {
  cliente: {},
  entrega: {},
  item: [],
  valorTotalItems: 0,
  pesoTotal: 0,
  medidaTotal: [],
  valorTotal: 0,
  cupom: null,
  desconto: 0,
  pagamento: {},
  tokenCard: {},
};

@Injectable({
  providedIn: "root",
})
export class CartService {
  private Éµcart: ICart = null;
  totalCmCubico=0
  constructor(
    @Inject(PLATFORM_ID) private platformId,
    private http: HttpClient
  ) {}

  get cart(): ICart {
    // Se jÃ¡ estÃ¡ definido, retorna
    if (this.Éµcart) {
      return this.Éµcart;
    }

    if (isPlatformServer(this.platformId)) {
      return (this.Éµcart = { ...cartInit });
    }

    if (isPlatformBrowser(this.platformId)) {
      let localS = JSON.parse(localStorage.getItem("cart"));
      if (!localS) {
        localS = { ...cartInit };
      }
      this.cart = localS;
    }

    this.updateTotalItems();
    this.updateTotalPeso();
    this.updateTotalMedidas();
    return this.Éµcart;
  }

  set cart(cart: ICart) {
    this.Éµcart = cart;
    this.updateTotalItems();
    this.updateTotalPeso();
    this.updateTotalMedidas();
    // Save localStorage
    this.saveLocal();
  }

  set entrega(entrega: any) {
    this.cart.entrega = entrega;
    localStorage.setItem("cartExpireAt", JSON.stringify(Date.now() + 86400000));
    this.saveLocal();
  }

  get entrega() {
    return JSON.parse(localStorage.getItem("cart")).entrega;
  }

  set item(item: any) {
    this.cart.item = item;
    this.saveLocal();
    this.updateTotalItems();
    this.updateTotalPeso();
    this.updateTotalMedidas();
  }

  set desconto(desconto: number) {
    this.cart.desconto = desconto;
    this.saveLocal();
  }

  set cupom(cupom: any) {
    this.cart.cupom = cupom;
    this.saveLocal();
  }

  get cupom() {
    return JSON.parse(localStorage.getItem("cart")).cupom;
  }

  set cliente(cliente: any) {
    this.cart.cliente = cliente;
    this.saveLocal();
  }

  set valorTotal(valorTotal: any) {
    this.cart.valorTotal = valorTotal;
    this.saveLocal();
  }

  get valorTotal() {
    return JSON.parse(localStorage.getItem("cart")).valorTotal;
  }

  set pagamento(pagamento: any) {
    this.cart.pagamento = pagamento;
    this.saveLocal();
  }
  set tokenCard(tokenCard: any) {
    this.cart.tokenCard = tokenCard;
    this.saveLocal();
  }

  saveLocal() {
    localStorage.setItem("cart", JSON.stringify(this.Éµcart));
  }

  addItem(item: IItem) {
    const foundIndex = this.Éµcart.item.findIndex((el) => el.id === item.id);
    if (foundIndex >= 0) {
      this.Éµcart.item[foundIndex].quantidade += item.quantidade;
    } else {
      this.Éµcart.item = [...this.Éµcart.item, item];
    }
    this.updateTotalItems();
    this.updateTotalPeso();
    this.updateTotalMedidas();
    this.saveLocal();
  }

  delItem(item: IItem) {
    this.Éµcart.item = new Collx(this.Éµcart.item)
      .remove("id", item.id)
      .toArray();
    this.updateTotalItems();
    this.updateTotalPeso();
    this.updateTotalMedidas();
    this.saveLocal();
  }

  resetCart() {
    this.cart = { ...cartInit };
  }

  updateItem(item: IItem) {
    this.Éµcart.item[this.Éµcart.item.indexOf(item)] = item;
    this.updateTotalItems();
    this.updateTotalPeso();
    this.updateTotalMedidas();
    this.saveLocal();
  }

  updateTotalItems() {
    this.Éµcart.valorTotalItems = 0;
    for (const item of this.Éµcart.item) {
      this.Éµcart.valorTotalItems +=
        (item.tabela?.vPromo || item.tabela?.vVenda) * item.quantidade;
    }
    this.saveLocal();
  }
  updateTotalPeso() {
    this.Éµcart.pesoTotal = 0;
    for (const item of this.Éµcart.item) {
      this.Éµcart.pesoTotal += item.peso * item.quantidade;
    }
      if(this.Éµcart.pesoTotal >= 31){
        this.Éµcart.pesoTotal=30
      }
  }
  updateTotalMedidas() {
    this.Éµcart.medidaTotal = 0;
    this.totalCmCubico = 0;
    for (const item of this.Éµcart.item) {
      let row_cm = (item.medidas[0] * item.medidas[1] * item.medidas[2]) * item.quantidade;
      this.totalCmCubico += row_cm;
      let raiz_cubica =  Math.round(Math.cbrt(this.totalCmCubico));
      this.Éµcart.medidaTotal = raiz_cubica;
    }
    if(this.Éµcart.medidaTotal >= 66){
      this.Éµcart.medidaTotal=65
    }
    if(this.Éµcart.medidaTotal <= 14){
      this.Éµcart.medidaTotal=15
    }

  }

  getFreteByCep(cep) {
    return this.http.post(`${environment.API_URL}transporte/getValorFrete`, {
      cep,
    });
  }

  getTransportePeriodo(empId) {
    return this.http.post(environment.API_URL + "transporte/getPeriodo", {
      empId,
    });
  }
  getTransporteMetodo(empId): Observable<any> {
    return this.http.post<any>(environment.API_URL + "transporte/getAll", {
      empId,
    });
  }

  getCorreiosPrazoPreco(altura, largura, comprimento, peso, cepOrigem, cepDestino): Observable<any> {
    return this.http.post(environment.API_URL + "correios/calc", {
      altura,
      largura,
      comprimento,
      peso,
      cepOrigem,
      cepDestino,
    });
  }
}
