import { Component, OnInit } from '@angular/core';
import { trigger, style, animate, transition} from '@angular/animations';
import { ErpInterceptor } from 'src/app/interceptors/erp-interceptor';
import { HttpClient } from '@angular/common/http';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { CartService } from 'src/app/services/cart.service';
import { ModalService } from 'src/app/services/modal.service';
import { MdMeusenderecosComponent } from '../../modais/md-meusenderecos/md-meusenderecos.component';
import { MdAlertComponent } from '../../modais/md-alert/md-alert.component';
import { MdNaoseicepComponent } from '../../modais/md-naoseicep/md-naoseicep.component';
import { validateDate, formatDateToISO } from '../../../core/Functions';
import { StoreService } from 'src/app/services/store.service';
import { environment } from 'src/environments/environment';
import {DD, IAngularMyDpOptions, IMyDateModel, IMyDivHostElement} from 'angular-mydatepicker';

declare var $: any;

@Component({
  selector: 'app-entrega',
  templateUrl: './entrega.component.html',
  styleUrls: ['./entrega.component.scss'],
  animations: [trigger('inOutAnimationMsg', [transition(':enter', [style({ height: 0, opacity: 0 }), animate('0.3s ease-out', style({ height: 285, opacity: 1 }))]), transition(':leave', [style({ height: 285, opacity: 1 }), animate('0.3s ease-in', style({ height: 0, opacity: 0 }))])])]
})
export class EntregaComponent implements OnInit {
  myOptions: IAngularMyDpOptions = {
    dateRange: false,
    dateFormat: 'dd/mm/yyyy',
    markCurrentDay:true,
    disableUntil: {
      year: new Date().getFullYear(),
      month: new Date().getUTCMonth() + 1, day: new Date().getDate() - 1
      }
          // other options...
  };



  public formGeral: FormGroup;
  public errors = [];
  public today = new Date().toISOString().substring(0, 10);
  private resFrete: number;
  public showDivMetodo = false;
  public showDivPeriodo = false;
  public showRest = false;
  public loadingCep = false;
  public anonimo = true;
  public periodos = [];
  public layout = environment.layout;
  public vFreteGratis = null;
  public periodo:any;
  public transporte:any;
  public metodoId:number;
  public metodoCorte:any;
  public week:any;
  public correios=false;
  public freteCorreio:number;
  public prazoCorreio:number;
  public periodoP:any;
  public dataFormat:any;
  locale: string = 'pt-br';
  constructor(private http: HttpClient,
              private fb: FormBuilder,
              private router: Router,
              public cartS: CartService,
              private modalS: ModalService,
              public activatedRoute: ActivatedRoute,
              private storeS: StoreService) { }


  ngOnInit(): void {
    $(() => {
      $('[data-toggle="popover"]').popover();
    });

    if (innerHeight < 850) {
      scrollTo(0, 300);
    }
    ErpInterceptor.useErpHeader = true;

    this.setupForms();

    this.consultarTransporte();
    this.selectTransporte();
    this.formGeral.patchValue({
      data: '',
      cep:'',
    });

  }
  setupForms() {
    this.formGeral = this.fb.group({
      cep: [null],
      data: [null],
      entregaBool: [null],
      entrCadastro: [false],
      periodo: ['7'],
      cartao: [this.layout === 'esaflores'],
      cartao_msg: [null],
      anonimo: [true],
      vFrete: [0],
      cartao_ass: [null],
      send_sms: [false],
      ocultarPedido: [false],
      entrega_nome: [null ],
      entrega_sobrenome: [null],
      entrega_cep: [null],
      entrega_logr: [null],
      entrega_numero: [null],
      entrega_complemento: [null],
      entrega_uf: [null],
      entrega_munic: [null],
      entrega_bairro: [null],
      entrega_prox: [null],
      entrega_fone: [null],
      entrega_fone2: [null],
      periodo_entrega: [this.periodoP],
      correios:[false],
      correios_prazo:[null]
    });

    if (Object.keys(this.cartS.entrega).length > 0 && JSON.parse(localStorage.getItem('cartExpireAt')) > Date.now()) {
        this.buscarCep(this.formGeral.get('cep').value);
      this.showDivMetodo = this.showDivPeriodo = this.showRest = false;
    } else if (this.activatedRoute.snapshot.data.cep) {
      this.formGeral.patchValue({cep: this.activatedRoute.snapshot.data.cep});
      this.buscarCep(this.formGeral.get('cep').value);
    }

    this.formGeral.patchValue(this.cartS.cart.entrega);
    this.formGeral.value.anonimo ?
      this.anonimo = true :
      this.anonimo = false;

    this.resFrete = this.formGeral.value.vFrete;
  }
  consultarTransporte(){
    this.cartS.getTransportePeriodo(this.storeS.layout.emp.id).subscribe((res: any) => {
      this.periodo = res.data || res;
      if (res.error) {
        this.modalS.createModal(MdAlertComponent, { title: 'Erro ao obter dados do perÃ­odo', message: 'Recarrega a pÃ¡gina e tente novamente ou espere alguns minutos. Se o erro persistir, entre em contato com a nossa equipe.' });
        return;
      }
      this.periodos = res.data || res;
    });
  }
    consultarCorreio(){
     return this.cartS.getCorreiosPrazoPreco(this.cartS.cart.medidaTotal,this.cartS.cart.medidaTotal, this.cartS.cart.medidaTotal, this.cartS.cart.pesoTotal, 80220060, this.formGeral.value.entrega_cep).subscribe((res: any) => {
        if (res.MsgErro) {
          this.modalS.createModal(MdAlertComponent, { title: 'Entrega nÃ£o disponivel para esse endereÃ§o', message: 'Infelizmente nÃ£o temos opÃ§Ã£o de entrega para esse endereÃ§o, consulte as opÃ§Ãµes de entrega pelos nossos canais de atendimento' });
          this.showDivPeriodo = false;
          this.showDivMetodo = false;
          this.correios=true
          return;
        }
        this.prazoCorreio = parseInt(res.cServico.PrazoEntrega) +1;

        let date = new Date()
        date.setDate(date.getDate() + parseInt(res.cServico.PrazoEntrega))
        const dayOfWeek = date.getDay()
        if (dayOfWeek === 6) date.setDate(date.getDate() + 2)
        if (dayOfWeek === 0) date.setDate(date.getDate() + 1)
        let model: IMyDateModel = {isRange: false, singleDate: {jsDate: new Date(date),formatted:("0" + ((new Date(date)).getDate())).slice(-2) + '/' +("0" + ((new Date(date)).getMonth() + 1)).slice(-2) + '/' + date.getFullYear() }, dateRange: null};
        this.formGeral.patchValue({data:model});
        this.correios=true
        this.freteCorreio = parseInt(res.cServico.Valor.toString().replace(",", "."));
      });
    }
  buscarCep(cep) {
    if (!cep) {
      return;
    }
    this.consultarTransporte();
    ErpInterceptor.useErpHeader = false;
    const validacep = /^[0-9]{8}$/;
    if (validacep.test(cep)) {
      try {
        this.loadingCep = true;
        this.formGeral.controls.data.disable();
        const urlViacep = `https://viacep.com.br/ws/${cep}/json/`;
        this.http.get(urlViacep).subscribe((res: any) => {
          if (res.erro) {
            this.formGeral.patchValue({cep: ''});
            this.modalS.createModal(MdAlertComponent, {title: 'CEP nÃ£o encontrado.', message: 'Verifique o CEP digitado e tente novamente.'});
            this.loadingCep = false;
            return;
          }

          this.formGeral.patchValue({
            cep,
            entrega_cep: cep,
            entrega_logr: res.logradouro,
            entrega_bairro: res.bairro,
            entrega_munic: res.localidade,
            entrega_uf: res.uf,
            entrega_complemento: res.complemento
          });
          this.getFreteByCep(cep);
        });
      } catch (error) {
        this.loadingCep = false;
        this.formGeral.controls.data.enable();
      }
    } else {
      this.formGeral.patchValue({cep: ''});
      this.modalS.createModal(MdAlertComponent, {title: 'CEP invÃ¡lido!', message: 'Digite um CEP vÃ¡lido.'});
    }
  }

  selectTransporte(){
    this.cartS.getTransporteMetodo(this.storeS.layout.emp.id).subscribe((res) => {
      if (res.error) {
        this.modalS.createModal(MdAlertComponent, { title: 'Erro ao obter dados do perÃ­odo', message: 'Recarrega a pÃ¡gina e tente novamente ou espere alguns minutos. Se o erro persistir, entre em contato com a nossa equipe.' });
        return;
      }
      this.transporte = res;
});
  }


  getFreteByCep(cep) {
    ErpInterceptor.useErpHeader = true;
    this.cartS.getFreteByCep(cep).subscribe((res: any) => {
      this.correios = false;
      this.loadingCep = false;
      this.formGeral.controls.data.enable();
      if (res.error || res.data?.error) {
        this.correios = true;
        this.filterTransportes()
        this.consultarCorreio();
      }
      this.showDivMetodo = true;
      if (this.formGeral.value.entregaBool === 'FROTA') {
        this.showDivPeriodo = true;
        this.showRest = true;
      }
      if (this.formGeral.value.entregaBool === 'TRANSP') {
        this.showDivPeriodo = true;
        this.showRest = true;
      }

      if (this.formGeral.value.entregaBool === 'RETIRA') {
        this.showDivPeriodo =   true;
        this.showRest = true;
        return;
      }
      this.correios=false;

      if (res.vFreteGratis) {
        this.vFreteGratis = Number(res.vFreteGratis);
        if (this.cartS.cart.valorTotalItems >= res.vFreteGratis) {
          this.formGeral.patchValue({vFrete: 0});
          this.resFrete = 0;
          return;
        }
      }
      this.formGeral.patchValue({vFrete: res.data?.vFrete || res.vFrete});
      this.resFrete = res.data?.vFrete || res.vFrete;
    });
  }

  changeRadioAnonimo($event) {
    $event.target.id === 'radioAnonimo' ?
      this.anonimo = true :
      this.anonimo = false;
    this.formGeral.patchValue({cartao_ass: null});
  }

  changePeriodo(Periodo) {
    this.periodoP=Periodo.periodo;
    this.showRest = true;
    this.metodoCorte = Periodo.corte;
    this.metodoCorte = this.metodoCorte.split(":");  this.metodoCorte = parseInt(this.metodoCorte[0],10) *100 + parseInt(this.metodoCorte[1],10)
  }
  filterTransportes(){
    if(this.correios === true){
      return this.transporte.filter( metodos => metodos.tipo ==='TRANSP'   );
    }else{
      return this.transporte.filter( metodos => metodos.tipo === 'FROTA' || metodos.tipo === 'RETIRA'    );
    }
  }



  changeTransporte(metodo) {
    if(this.formGeral.value.entregaBool=== "RETIRA"){
     this.formGeral.patchValue({vFrete: 0});
     this.showDivPeriodo = true;
     this.metodoId = metodo.id;
     this.showRest = true;
}
    if(this.formGeral.value.entregaBool === "FROTA"){
     this.formGeral.patchValue({vFrete: this.resFrete});
     this.showDivPeriodo=true;
     this.metodoId = metodo.id;
     this.showRest = true;
}

if(this.formGeral.value.entregaBool === "TRANSP"){
  this.formGeral.patchValue({vFrete: this.freteCorreio});
  this.showDivPeriodo=true;
  this.metodoId = metodo.id;
  this.showRest = true;
}
    this.errors = [];

    if (this.formGeral.value.periodo) {
      this.showRest = true;
    }
    this.formGeral.patchValue({periodo: null});


  }


  filterPeriodos() {
    let object = this.formGeral.value.data;
    let jsDate = new Date(object.singleDate?.jsDate);
    jsDate.setUTCHours(23,59,59,999);
    this.dataFormat=jsDate
    const d = new Date(jsDate );
    if (this.storeS.layout.emp.id === 1) {
        if(this.formGeral.value.entregaBool){
            return this.periodos.filter( transpId =>   transpId.ativo === (1) && transpId.transporte_id === (this.metodoId) && transpId.week[d.getDay()] === 1 ) ;
        }
}
    return this.periodos;
  }


  checkForm() {
    this.errors = [];

    if (!validateDate(this.formGeral.value.data.singleDate?.formatted)) {
      this.errors.push('Data com formato invÃ¡lido! Selecione a data de entrega novamente, se o problema persistir, favor entrar em contato com a nossa equipe.');
    }

    if (!this.formGeral.value.cep) {
      this.errors.push('Campo CEP da regiÃ£o obrigatÃ³rio');
    }

    if (!this.formGeral.value.data.singleDate?.formatted) {
      this.errors.push('Campo DATA obrigatÃ³rio.');
    } else {
      const calendarDate = new Date(this.dataFormat);
      const today = new Date();
      if (calendarDate.getDate() + calendarDate.getMonth() === today.getDate() + today.getMonth()) {
          if (today.getHours() *100 + today.getMinutes() >= this.metodoCorte) {
            this.errors.push('HorÃ¡rio de corte  atingido, favor escolher uma data posterior a atual.');
          }
      }
    }

    if (this.formGeral.value.entregaBool==='FROTA') {
      if (!this.formGeral.value.entrega_nome) {
        this.errors.push('Campo NOME de entrega obrigatÃ³rio.');
      }
      if (!this.formGeral.value.entrega_sobrenome) {
        this.errors.push('Campo SOBRENOME de entrega obrigatÃ³rio.');
      }
      if (!this.formGeral.value.entrega_cep) {
        this.errors.push('Campo CEP de entrega obrigatÃ³rio.');
      }
      if (!this.formGeral.value.entrega_logr) {
        this.errors.push('Campo ENDEREÃO de entrega obrigatÃ³rio.');
      }
      if (!this.formGeral.value.entrega_numero) {
        this.errors.push('Campo NÃMERO de entrega obrigatÃ³rio.');
      }
      if (!this.formGeral.value.entrega_uf) {
        this.errors.push('Campo UF de entrega obrigatÃ³rio.');
      }
      if (!this.formGeral.value.entrega_munic) {
        this.errors.push('Campo CIDADE de entrega obrigatÃ³rio.');
      }
      if (!this.formGeral.value.entrega_bairro) {
        this.errors.push('Campo BAIRRO de entrega obrigatÃ³rio.');
      }
      if (!this.formGeral.value.entrega_fone) {
        this.errors.push('Campo TELEFONE de entrega obrigatÃ³rio.');
      }
    } else {
      if (!this.formGeral.value.entrega_nome) {
        this.errors.push('Campo NOME de retirada obrigatÃ³rio.');
      }
      if (!this.formGeral.value.entrega_sobrenome) {
        this.errors.push('Campo SOBRENOME de retirada obrigatÃ³rio.');
      }
    }

    if (!this.formGeral.value.periodo) {
      this.errors.push('Selecione o perÃ­odo de entrega.');
    }

    if (this.formGeral.value.cartao && this.layout === 'esalflores') {
      if (!this.formGeral.value.cartao_msg) {
        this.errors.push('Escreva uma mensagem para o cartÃ£o de mensagem.');
      }
    }
  }

  clearEntrega() {
    this.formGeral.patchValue({
      vFrete: 0,
      entrega_cep: null,
      entrega_logr: null,
      entrega_numero: null,
      entrega_complemento: null,
      entrega_uf: null,
      entrega_munic: null,
      entrega_bairro: null,
      entrega_prox: null,
      entrega_fone: null,
      entrega_fone2: null
    });
  }

  clearRetirada() {
    this.formGeral.patchValue({
      vFrete: this.resFrete
    });
  }

  clearCorreio() {
    this.formGeral.patchValue({
      vFrete: this.freteCorreio
    });
  }

  modalMeusEnderecos() {
    this.modalS.createModal(MdMeusenderecosComponent).then(res => {
      if (res) {
        this.buscarCep(res.cep);
        this.formGeral.patchValue({
          cep: res.cep,
          entrega_cep: res.cep,
          entrega_logr: res.logradouro,
          entrega_numero: res.numero,
          entrega_complemento: res.complemento,
          entrega_uf: res.uf,
          entrega_munic: res.municipio,
          entrega_bairro: res.bairro
        });
        document.getElementById('inputData').focus();
      }
    });
  }

  openMdNaoSeiCep() {
    this.modalS.createModal(MdNaoseicepComponent).then(res => {
      if (res) {
        this.formGeral.patchValue({cep: res});
        this.buscarCep(res);
      }
    });
  }

  goToPagamento() {
    if (!validateDate(this.formGeral.value.data.singleDate?.formatted)) {
      this.formGeral.patchValue({ data: formatDateToISO(this.formGeral.value.data.singleDate?.formatted)});
    }

    this.checkForm();
    if (this.formGeral.invalid || this.errors.length > 0) {
      return;
    }
     this.formGeral.patchValue({
      periodo_entrega: this.periodoP,
      correios:this.correios,
      correios_prazo:this.prazoCorreio,
    });


    if(this.formGeral.value.entregaBool==='RETIRA'){
      this.clearEntrega();
      this.formGeral.value.entregaBool=false;
    }
    if(this.formGeral.value.entregaBool==='FROTA'){
      this.clearRetirada()
      this.formGeral.value.entregaBool=true;
    }
    if(this.formGeral.value.entregaBool==='TRANSP'){
      this.clearCorreio()
      this.formGeral.value.entregaBool=true;
    }

    this.formGeral.value.data= formatDateToISO(this.formGeral.value.data.singleDate?.formatted)

    // localStorage.setItem('form-entrega', JSON.stringify(this.formGeral.value));
    this.cartS.entrega = this.formGeral.value;
    this.router.navigate(['/pagamento']);
  }
}
